.skills-card{
    border-radius: 0.65rem;
    border: 1.5px solid var(--color5);
    padding: 2.5rem;
    text-align: center;
    background: rgba(22, 17, 47, 0.398);
    backdrop-filter: blur(1rem);
    position: relative;
    cursor: pointer;
    transition: all 0.3s ease;
}

.skills-card:hover ,
.skills-card.active{
    background: linear-gradient(90deg , var(--color8) 0% , var(--color7) 100%);
}

.skills-card span{
    font-size: 1.3rem;
    font-weight: 500;
}

.skills-icon{
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #2a2252;
    border-radius: 0.65rem;
    border: 1.5px solid #6852ba;
    position: absolute;
    top: -1rem;
    left: -1rem;
}

.skills-icon img {
    width: 3rem;
    height: auto;
    object-fit: cover;
}

@media (max-width: 1025px){
    .skills-card{
        padding: 2rem;
    }
    .skills-card span{
        font-size: 1rem;
    }
    .skills-icon{
        width: 3rem;
        height: 3rem;
    }
    .skills-icon img{
        width: 1.8rem;
    }
}

@media (max-width: 768px){
    .skills-card span{
        font-size: 0.9rem;
    }
}